import React, { useState } from 'react';
import styled from 'styled-components/macro';

const Navbar = () => {
    const [isOpen, setIsOpen] = useState(false)
    return (
        <Nav>
            <Logo href="">
                logo <span>code</span>
            </Logo>
            <Hamburger  onClick={() => setIsOpen(!isOpen)}>
                <span />
                <span />
                <span />
            </Hamburger>
            <Menu isOpen={isOpen}>
                <MenuLink href="">Our work</MenuLink>
                <MenuLink href="">about</MenuLink>
                <MenuLink href="">carrers</MenuLink>
                <MenuLink href="">contact</MenuLink>
            </Menu>
        </Nav>
    );
};

const Nav = styled.div`
    padding: 0 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    background: white;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    background-color: #F8F8F8;
`;

const Hamburger = styled.div`
    display: none;
    flex-direction: column;
    cursor: pointer;

    span {
        height: 2px;
        width: 25px;
        background: #7b7fda;
        margin-bottom: 4px;
        border-radius: 5px;
    }

    @media (max-width: 768px) {
        display: flex;
        z-index: 500; 
    }
`;

const MenuLink = styled.a`
    padding: 1rem 2rem;
    cursor: pointer;
    text-align: center;
    text-decoration: none;
    color: #67bc98;
    transition: all 0.3s ease-in;
    font-size: 2rem;

    &:hover {
        color: #7b7fda;
    }
`;

const Menu = styled.div`
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: relative;

    @media (max-width: 768px) {
        overflow: hidden;
        flex-direction: column;
        max-height: ${({ isOpen }) => (isOpen ? "300px" : "0")};
        transition: max-height 0.3s ease-in;
        width: 100%
    }
`;

const Logo = styled.a`
    padding: 1rem 0;
    color: #7b7fda;
    text-decoration: none;
    font-weight: 800;
    font-size: 2rem;

    span {
        font-weight: 300;
        font-size: 1.8rem;
    }
`;

export default Navbar;