import React from 'react';
import styled from 'styled-components/macro';

const Second = () => {
    return (
        <SecondContainer>
            <TextH5>
                Τοιχοποιία - Ξυλότυπος
            </TextH5>
            <Title>
                Entreprise de maçonnerie depuis 1756
            </Title>
        </SecondContainer>
    );
};

export default Second;

const SecondContainer = styled.div`
    height: 80vh;
    margin-top: 5rem;
    text-align: center;
    display: contents;
`;

const TextH5 = styled.h5`
    font-size: 3rem;
    margin-top: 10rem;
    letter-spacing: 0.2rem;
    position: relative;
    color: #787878;
`;

const Item = styled.div`
    display: flex; 
    justify-content: center;
    padding: .5rem;
    font-size: 1.8rem;
    font-weight: bold;
    background-color: transparent;
`;

const Grid = styled.div`
    justify-content: center;
    max-width: 1200px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 40rem;
    grid-gap: 4rem;
    margin-right: 7.5rem;
    margin-left: 7.5rem;

    @media (max-width: 600px) {
        grid-template-columns: repeat(1, 1fr);
    }

    @media (max-width: 900px) {
        grid-template-columns: repeat(1, 1fr);
    }
`;

const Title = styled.h1`
`;

const ImageMaison = styled.img`
    width: 25%;
    height: 25%;
    margin-top: 10rem;
`;

const TitleGrid = styled.h2`
    position: absolute;
    border-bottom-width: 1px;
    border-bottom-style: solid;
    border-bottom-color: black;
    font-size: 4rem;
`;

const TextGrid = styled.p`
    position: absolute;
    margin-top: 22rem;
    font-size: 1.5rem;
`;
