import React from "react";
import styled from "styled-components/macro";
import { FaYoutube, FaInstagram, FaTwitter, FaFacebook } from "react-icons/fa";
import Copyright from "./Copyright";

const Footer = () => {
  return (
    <FooterContainer>
      <FooterWrapper>
        <FooterRow>
          <FooterColumn>
            <FooterTitle>About Us</FooterTitle>
            <FooterLink href="#">Story</FooterLink>
            <FooterLink href="#">Clients</FooterLink>
            <FooterLink href="#">Testimonials</FooterLink>
          </FooterColumn>
          <FooterColumn>
            <FooterTitle>Services</FooterTitle>
            <FooterLink href="#">Marketing</FooterLink>
            <FooterLink href="#">Consulting</FooterLink>
            <FooterLink href="#">Development</FooterLink>
            <FooterLink href="#">Design</FooterLink>
          </FooterColumn>
          <FooterColumn>
            <FooterTitle>Contact Us</FooterTitle>
            <FooterLink href="#">United States</FooterLink>
            <FooterLink href="#">United Kingdom</FooterLink>
            <FooterLink href="#">Australia</FooterLink>
            <FooterLink href="#">Support</FooterLink>
          </FooterColumn>
          <FooterColumn>
            <FooterTitle>Social</FooterTitle>
            <FooterLink href="www.facebook.com">
              <Icon className="fab"> <FaFacebook /> </Icon>
              Facebook
            </FooterLink>
            <FooterLink href="#">
              <Icon className="fab"><FaInstagram /></Icon>
              Instagram
            </FooterLink>
            <FooterLink href="#">
              <Icon className="fab"><FaYoutube/></Icon>
              Youtube
            </FooterLink>
            <FooterLink href="#">
              <Icon className="fab"><FaTwitter /></Icon>
              Twitter
            </FooterLink>
          </FooterColumn>
        </FooterRow>
      </FooterWrapper>
      <Copyright />
    </FooterContainer>
  );
};

export default Footer;

const FooterContainer = styled.div`
`;

const FooterWrapper = styled.div`
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 1000px;
    margin: 0 auto;
    /* background: red; */
`

const FooterColumn = styled.div`
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-left: 60px;
`;

const FooterRow = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(230px, 1fr));
  grid-gap: 20px;
  @media (max-width: 1000px) {
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  }
`;

const FooterLink = styled.a`
  color: #000000;
  margin-bottom: 20px;
  font-size: 18px;
  text-decoration: none;
  &:hover {
      color: #7b7fda;
      transition: 50ms ease-in;
  }
`;

const FooterTitle = styled.p`
  font-size: 24px;
  color: #000000;
  margin-bottom: 40px;
  font-weight: bold;
`;

const Icon = styled.i`
  font-size: 18px;
  margin-right: 16px;
`;
