import styled from 'styled-components/macro';

const Button = styled.button`
    top: 50%;
    right: 47%;
    border: none;
    font-weight: 600;
    cursor: pointer;
        
    &:disabled {
        cursor: not-allowed;
    }
    
    &:hover {
        opacity: 0.8;
    }
`;

export default Button;