import styled from "styled-components/macro";
import ThreeBars from "../../image/header/hamburger.png";
import Maison from "../../image//header/maison.png";
import Tools from "../../image/header/tools.png";

export default function Test() {
  return (

    <Grid>
      <Card>
        <a href="#">
            <img
              className="image"
              src={ThreeBars}
              alt="Maison House Icon"
            />
        </a>
        <CardTitle>Devis</CardTitle>
        <CardText>
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Sit vero
          eius quae culpa numquam illum mollitia?
        </CardText>
      </Card>
      <Card>
        <a href="#">
        <img
          className="image"
          src={Maison}
          alt="Maison House Icon"
        />
        </a>
        <CardTitle>
          Construction
        </CardTitle>
        <CardText>
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Sit vero
          eius quae culpa numquam illum mollitia?
        </CardText>
      </Card>
      <Card>
        <a href="#">
        <img
          className="image"
          src={Tools}
          alt="Maison House Icon"
        />
        </a>
        <CardTitle>
          Savoir faire
        </CardTitle>
        <CardText>
          Lorem ipsum dolor sit, amet consectetur adipisicing elit. Sit vero
          eius quae culpa numquam illum mollitia?
        </CardText>
      </Card>
    </Grid>
  );
}

const Grid = styled.div`
  display: grid;
  max-width: 1100px;
  margin: auto;
  margin-top: 5rem;
  margin-bottom: 5rem;
  grid-gap: 1.5rem;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
`;

const Card = styled.div`
  margin: auto;
  padding: 16px;
  border-radius: 5px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  justify-content: center;

  .image {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }
`;


const CardTitle = styled.h3`
  text-align: center;

`;

const CardText = styled.p`
    font-size: 1.5rem;
    padding: 2.5rem;
    text-align: center;
    border-radius: 3px;
    transition: transform 0.3s;
`;